<template>
    <div class="page-content">
        <div class="page-block order-statuses-wrapper mb30 page-padding-y">
            <div v-if="width > 900" class="order-statuses flex-start">
                <div v-for="s in statuses" :key="s.key"
                     :class="[{'order-status-active': s.key == status.key}, 'order-status']"
                     @click="status = s">
                    <span>{{s.title}}</span>
                </div>
            </div>
            <div v-else class="order-statuses">
                <div ref="sort_block1" class="sort-block" @click="show_dropdown1 = !show_dropdown1">
                    <span class="current">{{status.title}}</span>
                    <span class="icon"></span>
                    <ul class="dropdown"
                        v-if="show_dropdown1"
                        v-click-outside="{
                          exclude: ['sort_block1'],
                          handler: 'onClose1'
                        }">
                        <li v-for="s in statuses" :key="s.key" @click="status = s">{{s.title}}</li>
                    </ul>
                </div>
            </div>
            <div class="order-filters flex-between">
                <div class="input-wrap-short">
                    <label>ID</label>
                    <input type="text" placeholder="2015345" v-model="order_id">
                </div>
                <div class="input-wrap-short">
                    <label>Дата с</label>
                    <date-picker
                            v-model="date_from"
                            :lang="lang"
                            :time-title-format="'MMMM-DD-YYYY'">
                    </date-picker>
                </div>
                <div class="input-wrap-short">
                    <label>Дата по</label>
                    <date-picker
                            v-model="date_to"
                            :lang="lang"
                            :time-title-format="'MMMM-DD-YYYY'">
                    </date-picker>
                </div>
                <button class="button-gradient">Применить</button>
            </div>
        </div>

        <div class="page-block mb30 operations-block">
            <div class="operations-block__head">
                <div class="flex-start">
                    <div class="title">Сортировать по:</div>
                    <div ref="sort_block" class="sort-block" @click="show_dropdown = !show_dropdown">
                        <span class="current">{{sort.title}}</span>
                        <span class="icon"></span>
                        <ul class="dropdown"
                            v-if="show_dropdown"
                            v-click-outside="{
                              exclude: ['sort_block'],
                              handler: 'onClose'
                            }">
                            <li v-for="s in sort_list" :key="s.key" @click="sort = s">{{s.title}}</li>
                        </ul>
                    </div>
                </div>
            </div>

            <div class="table-wrapper" v-if="width > 900">
                <table border="1" class="table">
                    <thead>
                    <tr>
                        <th>Дата</th>
                        <th>Вид события</th>
                        <th>ID</th>
                        <th>Сумма</th>
                        <th>Баланс</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="operation in operations" :key="operation.id">
                        <td>{{operation.date}} <span class="time">{{operation.time}}</span></td>
                        <td>{{operation.type}}</td>
                        <td>{{operation.id}}</td>
                        <td><span :class="[
                            {'green': operation.type_id == 1},
                            {'red': operation.type_id == 2}
                        ]">{{new Intl.NumberFormat('ru-RU').format(operation.amount)}} Р</span></td>
                        <td>{{new Intl.NumberFormat('ru-RU').format(operation.balance)}} Р</td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div class="operation__items" v-else>
                <div class="operation__item" v-for="operation in operations" :key="operation.id">
                    <div class="operation__item__data flex-start">
                        <div class="title">Дата</div>
                        <div class="value">{{operation.date}} <span class="time">{{operation.time}}</span></div>
                    </div>
                    <div class="operation__item__data flex-start">
                        <div class="title">Тип события</div>
                        <div class="value">{{operation.type}}</div>
                    </div>
                    <div class="operation__item__data flex-start">
                        <div class="title">ID</div>
                        <div class="value">{{operation.id}}</div>
                    </div>
                    <div class="operation__item__data flex-start">
                        <div class="title">Сумма</div>
                        <div class="value"><span :class="[
                            {'green': operation.type_id == 1},
                            {'red': operation.type_id == 2}
                        ]">{{new Intl.NumberFormat('ru-RU').format(operation.amount)}} Р</span></div>
                    </div>
                    <div class="operation__item__data flex-start">
                        <div class="title">Баланс</div>
                        <div class="value">{{new Intl.NumberFormat('ru-RU').format(operation.balance)}} Р</div>
                    </div>
                </div>
            </div>
        </div>

        <div class="news-block-wrapper page-block page-padding-y">
            <h1>Свежие новости</h1>
            <div class="news-block">
                <div v-for="article in news" :key="article.id" class="news-item flex-between align-top">
                    <div class="text">
                        <h3>{{article.title}}</h3>
                        <p>{{article.text}}</p>
                    </div>
                    <div class="image">
                        <img :src="genImageUrl(article)" alt="">
                    </div>
                </div>
            </div>
            <div class="load-more">Ещё новости</div>
        </div>
    </div>
</template>

<script>
    import DatePicker from 'vue2-datepicker';
    import 'vue2-datepicker/index.css';

    export default {
        name: "Operations",
        components: {DatePicker},
        props: ['width', 'news'],
        data: () => ({
            show_dropdown: false,
            show_dropdown1: false,
            status: {
                title: 'Все',
                key: 'all'
            },
            statuses: [
                {
                    title: 'Все',
                    key: 'all'
                },
                {
                    title: 'Оплата',
                    key: 'active'
                },
                {
                    title: 'Пополнение',
                    key: 'finished'
                },
                {
                    title: 'Возврат',
                    key: 'canceled'
                },
            ],
            sort: {
                title: 'Дата убывания',
                key: 'date1'
            },
            sort_list: [
                {
                    title: 'Дата убывания',
                    key: 'date1'
                },
                {
                    title: 'Дата возрастания',
                    key: 'date2'
                }
            ],
            order_id: null,
            date_from: null,
            date_to: null,
            lang: {
                formatLocale: {
                    monthsShort: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
                    weekdaysMin: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
                    firstDayOfWeek: 1,
                }
            },
            operations: [
                {
                    date: '01.03.2020',
                    time: '12:00:00',
                    type_id: 1,
                    type: 'Пополнение баланса',
                    id: 123456789,
                    amount: 100000,
                    balance: 100000

                },
                {
                    date: '01.03.2020',
                    time: '12:00:20',
                    type_id: 2,
                    type: 'Оплата заказа',
                    id: 123456781,
                    amount: 20000,
                    balance: 80000

                },
                {
                    date: '01.03.2020',
                    time: '12:00:20',
                    type_id: 1,
                    type: 'Возврат средств',
                    id: 123456741,
                    amount: 20000,
                    balance: 100000

                }
            ]
        }),
        methods: {
            genImageUrl(article) {
                return `${process.env.VUE_APP_SERVER_URL}${article.image}`
            },
            onClose() {
                this.show_dropdown = false
            },
            onClose1() {
                this.show_dropdown1 = false
            }
        }
    }
</script>

<style scoped>

</style>